<template>
	<view class="body">
		<view class="title">
			<view class="top">
				感謝您使用預約配送功能，
			</view>
			<view class="buttom">
				您將獲得乙次紅包三選一的抽獎活動!!
			</view>
		</view>
		<view class="main">
			<view :class="show?'shake':''" class="red" @click="flip()">
				<image class="img" src="../../static/img/2055.png" mode="widthFix"></image>
			</view>
			<view class="red">
				<image class="img" src="../../static/img/2055.png" mode="widthFix"></image>
			</view>
			<view class="red">
				<image class="img" src="../../static/img/2055.png" mode="widthFix"></image>
			</view>
		</view>
		<view class="windows" v-if="show1">
			<view class="text">恭喜抽中棒棒糖一枚！</view>
			<view class="close" @click="close()">
				<image class="img" src="../../static/img/cha.png" mode="widthFix" />
			</view>
		</view>
		<view class="opacity" v-if="show1"></view>

	</view>
</template>

<script>
	import {
		pre
	} from '@/static/js/preview.js'
	export default {
		data() {
			return {
				show:false,
				show1:false,
			};
		},
		methods:{
			flip(){
				let that = this
				that.show = true
				setTimeout(function(){
					that.show = false
					that.show1 = true
				},2000)
			},
			close(){
				this.show1 = false
			}
		}
	}
</script>

<style lang="less">
	@import url("@/static/css/content.css");

	.body {
		background: url(../../static/img/16.png) no-repeat;
		background-size: 100% 100%;
		height: 100vh;
	}

	.title {
		padding: 100rpx 80rpx 100rpx;
		color: #FF0000;
		font-size: 34rpx;
		font-weight: 700;
	}

	* {
		margin: 0;
		padding: 0;
		list-style: none;
		border: 0;
	}

	@-webkit-keyframes shake {
		0% {
			-webkit-transform: rotate(2deg) translate3d(0, 0, 0)
		}

		50% {
			-webkit-transform: rotate(-2deg) translate3d(0, 0, 0)
		}

		100% {
			-webkit-transform: rotate(2deg) translate3d(0, 0, 0)
		}
	}

	@-moz-keyframes shake {
		0% {
			transform: rotate(2deg) translate3d(0, 0, 0)
		}

		50% {
			transform: rotate(-2deg) translate3d(0, 0, 0)
		}

		100% {
			transform: rotate(2deg) translate3d(0, 0, 0)
		}
	}

	@-ms-keyframes shake {
		0% {
			transform: rotate(2deg) translate3d(0, 0, 0)
		}

		50% {
			transform: rotate(-2deg) translate3d(0, 0, 0)
		}

		100% {
			transform: rotate(2deg) translate3d(0, 0, 0)
		}
	}

	.main {
		padding: 0 30rpx;
		display: flex;
		justify-content: space-around;
	}

	.red {
		width: 200rpx;
		height: 240rpx;
		border-radius: 15px;
		box-shadow: 1px 1px 20px #666;
		overflow: hidden;
		transform-origin: 50% 100%;
		-webkit-transform-origin: 50% 100%;
	}

	.red .img {
		width: 100%;
		height: auto;
	}

	.red.shake {
		animation: shake .2s infinite linear;
		-webkit-animation: shake .2s infinite linear;
	}

	.windows {
		width: 300px;
		height: 200px;
		position: absolute;
		top: 50%;
		margin-top: -100px;
		left: 50%;
		margin-left: -150px;
		border-radius: 15px;
		background: #fff;
		z-index: 11;
	}

	.text {
		text-align: center;
		font-size: 18px;
		font-family: "微软雅黑";
		vertical-align: middle;
		padding-top: 60px;
	}

	.close {
		width: 30px;
		height: 30px;
		position: absolute;
		right: -10px;
		top: -10px;
		background: #fff;
		border-radius: 50%;
		cursor: pointer;
		display: flex;
		justify-content: center;
		align-items: center;
		transition: all 0.5s linear;
	}

	.close:hover {
		transform: rotate(180deg);
	}

	.close .img {
		// padding-top: 5px;
		width: 20rpx;
	}

	.opacity {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: #000;
		opacity: 0.5;
		filter: alpha(opacity=50);
		z-index: 10;
		width: 100%;
		height: 100%;
	}
</style>
